import React, { useContext } from 'react';
import GaugeChart from 'react-gauge-chart';
import { nanoid } from 'nanoid';
import Container from 'react-bootstrap';
import PortfolioContext from '../../context/context';

const Cringeometer = ({id, percent}) => {
    const { isMobile } = useContext(PortfolioContext);
    const chartStyle = {
        width: isMobile ? "260" : "260",
      }
       

    return (
        <GaugeChart 
            id={id} 
            className="cringeometer"
            nrOfLevels={10} 
            arcPadding={0.1} 
            cornerRadius={3} 
            percent={percent} 
            formatTextValue={(_) => "Cringe-o-meter"}
            textColor="#EEEEEE"
            style={chartStyle}
        />
    );
};

export default Cringeometer;