import React, { useContext, useEffect, useState } from 'react';
import Fade from 'react-reveal/Fade';
import { Container, Row, Col } from 'react-bootstrap';
import PortfolioContext from '../../context/context';
import Title from '../Title/Title';
import EmbeddedYoutubeVideo from './EmbeddedYoutubeVideo'
import Cringeometer from './../Core/Cringeometer'

const Videos = () => {
  const { videos } = useContext(PortfolioContext);

  const [isDesktop, setIsDesktop] = useState(false);
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    if (window.innerWidth > 769) {
      setIsDesktop(true);
      setIsMobile(false);
    } else {
      setIsMobile(true);
      setIsDesktop(false);
    }
  }, []);

  return (
    <section id="videos">
      <Container>
        <div className="video-wrapper">
          <Title title="Videos" />
          {videos.map((v) => {
            const { title, info, info2, id, videoId, year, cringe } = v;

            return (
              <Row key={id}>
                <Col lg={4} sm={12}>
                  <Fade
                    left={isDesktop}
                    bottom={isMobile}
                    duration={1000}
                    delay={100}
                    distance="30px"
                  >
                    <div className="video-wrapper__text">
                      <h3 className="video-wrapper__text-title">{title}</h3>
                      <div>
                        <p>
                          {info}
                        </p>
                        <p className="mb-4">{info2}</p>
                        <p className="mb-4">{year}</p>
                      </div>

                    </div>

                  </Fade>
                  
                  
                  {cringe &&
                    <Fade
                    left={isDesktop}
                    bottom={isMobile}
                    duration={1000}
                    delay={100}
                    distance="30px"
                  >
                    <Cringeometer id={`cringeometer_${id}`} percent={cringe} />
                  </Fade>
                 }
                </Col>
                <Col lg={8} sm={12}>
                  <Fade
                    right={isDesktop}
                    bottom={isMobile}
                    duration={1000}
                    delay={200}
                    distance="30px"
                  >
                    <div className="video-wrapper__image">
                      <EmbeddedYoutubeVideo id={videoId} />
                    </div>
                  </Fade>
                </Col>
              </Row>
            );
          })}
        </div>
      </Container>
    </section>
  );
};

export default Videos;
