import React, { useContext, useState, useEffect } from 'react';
import { Container } from 'react-bootstrap';
import Fade from 'react-reveal/Fade';
import { Link } from 'react-scroll';
import PortfolioContext from '../../context/context';

const Header = () => {
  const { hero } = useContext(PortfolioContext);
  const [isDesktop, setIsDesktop] = useState(false);
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    if (window.innerWidth > 769) {
      setIsDesktop(true);
      setIsMobile(false);
    } else {
      setIsMobile(true);
      setIsDesktop(false);
    }
  }, []);

  return (
    <section id="hero" className="jumbotron">
      <Container>
        <Fade duration={1000} delay={500} distance="30px">
          <h1 className="hero-title">
            Every now and then I get the urge to create something and throw it onto an unsuspecting Internet.
          </h1>
        </Fade>
        <Fade duration={1000} delay={2500} distance="30px">
          <h1 className="hero-title">
            This is where they gather.
          </h1>
        </Fade>
        <Fade left={isDesktop} bottom={isMobile} duration={1000} delay={3500} distance="30px">
          <p className="hero-cta">
            {hero.links && hero.links.map((l) => 
              <span className="cta-btn cta-btn--hero" key={l.id}>
                <Link to={l.sectionId} smooth duration={1000}>
                  {l.caption}
                </Link>
              </span>
              )
            }
          </p>
        </Fade>
      </Container>
    </section>
  );
};

export default Header;
